import React, {useState, useEffect} from 'react';
import navbar_logo from '../images/navbar_logo_white.png'

import '../css/navbar.css';
import MenuIcon from '@material-ui/icons/Menu';
import { makeStyles } from "@material-ui/core/styles";

const Styles = makeStyles(theme => ({
  icon: {
    color: 'white',
    fontSize: '36px',
    }
  }
));

function NavComponent() {
    const classes = Styles();
    const [displayBool, changeBool] = useState('none');

    function handleIconClick() {
      if (displayBool === 'none') {
        changeBool('block')
      } else if (displayBool === 'block' || window.innerWidth > 700) {
        changeBool('none')
      }
    }

    function handleResize (e) {
      const windowSize = window.innerWidth;
      
      if (windowSize > 700) {
        changeBool('none')
      }
    }

    useEffect(() => {
      window.addEventListener("resize",handleResize);
    })

      return (
        <div className="navbar_container">
          <div className="navbar">
            <img src={navbar_logo} alt="navbar logo" />
            <a href="/" className="Link"> Home </a>
            <a href="/" className="Link"> About me </a>
            <a href="/" className="Link"> Career </a>
            <a href="/" className="Link"> Projects </a>
            <a href="/" className="Link"> Contact </a>
            <div className="navIcon" onClick={handleIconClick}>
               <MenuIcon className={classes.icon} />
            </div>
          </div>
          <div className="dropdown" style={{display: `${displayBool}`}}> <a href="/"> Home </a></div>
          <div className="dropdown" style={{display: `${displayBool}`}}> <a href="/"> About me </a></div>
          <div className="dropdown"style={{display: `${displayBool}`}}> <a href="/"> Career </a></div>
          <div className="dropdown" style={{display: `${displayBool}`}}> <a href="/"> Projects </a></div>
          <div className="dropdown" style={{display: `${displayBool}`}}> <a href="/"> Contact </a></div>
        </div>
          )
}

export default NavComponent;
